<template>
<div>
  <v-container>
    <v-card class="mb-2">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Start An Audit</v-card-title>
      <v-card-text>
        <v-subheader class="pa-0">Physician</v-subheader>
        <v-layout row wrap>
          <v-flex xs12 md6>
            <v-autocomplete
              v-model="selectedNpi"
              :items=physicianLookup
              persistent-hint
              item-text="name"
              item-value="npi"
              @change="selectPhysician(selectedNpi)"
            >
            </v-autocomplete>
          </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu1"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditBeginDate"
                label="Begin Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditBeginDate" no-title @input="menu1 = false"></v-date-picker>
            </v-menu>
        </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu2"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditEndDate"
                label="End Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditEndDate" no-title @input="menu2 = false"></v-date-picker>
            </v-menu>            
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12>
            <v-btn block color="blue" @click="searchProcedures()">Search</v-btn>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
  </v-container>
</div>
</template>

<script>
import Axios from "axios";

export default {
  name: "Main",
  components: {},
  data: () => ({
    auditBeginDate: "",
    auditEndDate: "",
    selectedNpi: "",
    menu1: false,
    menu2: false,
    procedureListing: []
  }),
  mounted() {
    //   this.currentPhysician = this.changer("");
  },
  computed: {
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    }
  },
  methods: {
    async searchProcedures() {
      // not needed elsewhere.  just doing it here.
      npi = beginDate = endDate = xx;
      this.procedureListing = await Axios.get(Config.apiRoot + "/procedures");
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
 
</script>