<template>
<div>
  <v-container>
    <v-card class="mb-2">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Search</v-card-title>
      <v-card-text>
        <v-subheader class="pa-0">Physician Lookup<i class="material-icons" @click="newPhysician()" style="padding-left:8px;">add_circle_outline</i></v-subheader>
        <v-autocomplete
          v-model="selectedNpi"
          :items=physicianLookup
          persistent-hint
          item-text="name"
          item-value="npi"
          @change="selectPhysician(selectedNpi)"
        >
        </v-autocomplete>
      </v-card-text>
    </v-card>
    <v-card v-if="showPhysicianProfile">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Profile</v-card-title>
      <v-card-text>
        <v-alert v-model="alertMessage.show" dismissible :type=alertMessage.type>{{ alertMessage.title }}</v-alert>
        <v-form ref="form">
          <v-text-field v-model="currentPhysician.action" label="Action Type" v-show="false"></v-text-field>
          <v-container fluid>
            <v-layout row wrap> 
              <v-flex xs12 md6>
                <v-text-field v-model="currentPhysician.npi" maxlength="10" v-validate="'required|numeric'" label="NPI"></v-text-field>
                <span>{{ errors.first('npi') }}</span>
              </v-flex>
              <v-flex xs12 md6>
                <v-text-field v-model="currentPhysician.specialty" label="Specialty"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md2>
                <v-text-field v-model="currentPhysician.title" label="Title"></v-text-field>
              </v-flex>
              <v-flex xs12 md5>
                <v-text-field v-model="currentPhysician.last_name" label="Last Name"></v-text-field>
              </v-flex>
              <v-flex xs12 md5>
                <v-text-field v-model="currentPhysician.first_name" label="First Name"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs2>
                <v-subheader v-text="'Submitting Bills'"></v-subheader>
              </v-flex>
              <v-flex xs10>
                <v-radio-group v-model="currentPhysician.billing" row>
                  <v-radio label="Yes" value="Y"></v-radio>
                  <v-radio label="No" value="N"></v-radio>
                </v-radio-group>
              </v-flex>
            </v-layout>
            <p>Primary Address</p>
            <v-layout row wrap>
              <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location1_address1" label="Address1"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location1_address2" label="Address2"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_city" label="City"></v-text-field>
              </v-flex>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_state" label="State"></v-text-field>
              </v-flex>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_zip" label="Zip"></v-text-field>
              </v-flex>
            </v-layout>

            <p>Additional Address</p>
            <v-layout row wrap>
              <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location2_address1" label="Address1"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location2_address2" label="Address2"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_city" label="City"></v-text-field>
              </v-flex>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_state" label="State"></v-text-field>
              </v-flex>
              <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_zip" label="Zip"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12>
                <v-select multiple chips v-model="currentPhysician.physician_entity_id"  :items="entities" item-text="entity_name" item-value="id" label="Entity"></v-select>
                <PhysicianEntity v-for="ent in currentPhysician.physician_entity_id" v-bind:key="ent.entity_id" :entity="getEntity(ent)"></PhysicianEntity>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12>
                <v-select v-model="currentPhysician.physician_practice_id" @change="getPractice()" :items="practices" item-text="group_practice" item-value="id" label="Practice"></v-select>
                <PhysicianPractice :practice="getPractice()"></PhysicianPractice>
              </v-flex>
            </v-layout>
            <v-layout row wrap>  
              <v-flex xs12>
                <v-textarea v-model="currentPhysician.comments" box label="Comments" auto-grow></v-textarea>
              </v-flex>
            </v-layout>
            <v-layout row wrap>  
              <v-flex xs4>
                <v-checkbox v-model="currentPhysician.terminated" label="Terminated"></v-checkbox>
              </v-flex>
              <v-flex xs8>
                <!-- <v-text-field v-model="currentPhysician.terminatedDate" v-if="this.currentPhysician.terminated" label="TerminatedDate" disabled></v-text-field> -->
                <v-dialog  v-if="this.currentPhysician.terminated" ref="terminatedDateDialog" v-model="terminatedDateModal" :return-value.sync="currentPhysician.terminatedDate" persistent lazy full-width width="290px">
                  <v-text-field
                    slot="activator"
                    v-model="currentPhysician.terminatedDate"
                    label="Terminated Date"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker v-model="terminatedDatePicker" scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="terminatedDateModal = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.terminatedDateDialog.save(terminatedDatePicker)">OK</v-btn>
                  </v-date-picker>
                </v-dialog>
              </v-flex>
            </v-layout>
          </v-container>

          <v-btn @click="savePhysician()">Save</v-btn>
          <v-btn @click="cancelPhysician()">Cancel</v-btn>
        </v-form>
      </v-card-text> 
    </v-card>
  </v-container>
  <confirmation-dialog ref="confirmationDialog" title="Confirm" message="Are you sure you want to cancel your changes?"></confirmation-dialog>
</div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import PhysicianEntity from "@/components/PhysicianAdministration/Entity";
import PhysicianPractice from "@/components/PhysicianAdministration/Practice";

const initializePhysician = () => {
  return {
    action: "",
    npi: "",
    last_name: "",
    first_name: "",
    title: "",
    specialty: "",
    billing: "",
    practice_location1_address1: "",
    practice_location1_address2: "",
    practice_location1_city: "",
    practice_location1_state: "",
    practice_location1_zip: "",
    practice_location2_address1: "",
    practice_location2_address2: "",
    practice_location2_city: "",
    practice_location2_state: "",
    practice_location2_zip: "",
    physician_entity_id: "",
    physician_practice_id: 0,
    comments: "",
    terminated: 0,
    terminatedDate: ""
  };
};

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    PhysicianEntity,
    PhysicianPractice
  },
  data: () => ({
    terminatedDatePicker: "",
    terminatedDateModal: "",
    selectedNpi: "",
    showPhysicianProfile: false,
    alertMessage: { show: false, type: "info", message: "" },
    currentPhysician: initializePhysician()
  }),
  mounted() {
    //   this.currentPhysician = this.changer("");
  },
  computed: {
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    }
  },
  methods: {
    newPhysician: function() {
      this.currentPhysician = initializePhysician(); // re-initialize fields
      this.currentPhysician.action = "new"; // adding a new physician
      this.alertMessage.show = false; // if it's shown from a previous action
      this.showPhysicianProfile = true;
    },
    selectPhysician: function(sel) {
      this.currentPhysician = this.physicians.find(p => p.npi === sel);
      if (!Array.isArray(this.currentPhysician.physician_entity_id)) {
        // convert string of numbers to array of integers.  TODO - is there a better way?
        this.currentPhysician.physician_entity_id = this.currentPhysician.physician_entity_id.split(",").map(el => {
          let n = Number(el);
          return n === 0 ? n : n || el;
        });
      }
      this.currentPhysician.action = "update"; // updating an existing physician
      this.alertMessage.show = false; // if it's shown from a previous action
      this.showPhysicianProfile = true;
    },
    savePhysician: function() {
      let dispatchType = "";
      if (this.currentPhysician.action === "new") {
        dispatchType = "NEW_PHYSICIAN";
      } else {
        dispatchType = "UPDATE_PHYSICIAN";
      }
      this.$store
        .dispatch(dispatchType, this.currentPhysician)
        .then(data => {
          if (data === true) {
            this.showMessage("success", "Record sucessfully saved");
          } else {
            this.showMessage("error", "There was a problem saving your changes.  MSG - " + data);
          }
        })
        .catch(err => {
          console.log("err - ", err);
          this.showMessage("error", "There was a problem saving your changes.  MSG - " + err);
        });
    },
    cancelPhysician: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.showPhysicianProfile = false;
        }
      });
    },
    getEntity: function(id) {
      return this.entities.find(e => e.id == id);
    },
    getPractice: function() {
      return this.practices.find(e => e.id == this.currentPhysician.physician_practice_id);
    },
    setTerminated: function() {
      if (this.currentPhysician.terminated) {
        this.currentPhysician.terminatedDate = new Date(); // set date
      } else {
        this.currentPhysician.terminatedDate = "";
      }
    },
    showMessage: function(type, message) {
      this.alertMessage.type = type;
      this.alertMessage.title = message;
      this.alertMessage.show = true;
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
 